import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Navbar, Nav, Container, Modal } from 'react-bootstrap';
import './NavBar.css';
import { Link } from 'react-router-dom';
import Login from './Login';
import Register from './Register';

const NavBar = () => {
  const [showMenuModal, setShowMenuModal] = useState(false);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showSignupModal, setShowSignupModal] = useState(false);

  const [isLoggedIn, setIsLoggedIn] = useState(false); // Track the user's authentication status

  const handleMenuToggle = () => {
    setShowMenuModal(!showMenuModal);
  };

  const handleMenuModalClose = () => {
    setShowMenuModal(false);
  };

  const handleLoginToggle = () => {
    setShowLoginModal(!showLoginModal);
  };

  const handleLoginModalClose = () => {
    setShowLoginModal(false);
  };

  const handleSignupToggle = () => {
    setShowSignupModal(!showSignupModal);
  };

  const handleSignupModalClose = () => {
    setShowSignupModal(false);
  };

  // Function to check if the user is logged in
  const checkUserLoggedIn = async () => {
    try {
      // Make a request to the backend to check the user's authentication status
      const response = await axios.get('http://localhost:3000/check-auth');
      if (response.data.success) {
        setIsLoggedIn(true);
      }
    } catch (error) {
      // Handle error if any
    }
  };

  // Call checkUserLoggedIn on component mount
  useEffect(() => {
    checkUserLoggedIn();
  }, []);

  const handleLogout = async () => {
    try {
      // Perform logout actions on the backend
      await axios.post('http://localhost:3000/logout');
      setIsLoggedIn(false); // Update the state to indicate the user is logged out
    } catch (error) {
      // Handle error if any
    }
  };

  return (
    <div>
      <Navbar className="navbar fixed-top bg-dark navbar-dark" expand="lg">
        <Container fluid>
          <Navbar.Brand className="ml-auto brand-font">
            Student Rental
            <div className="line"></div>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarNav" onClick={handleMenuToggle} />
          <Navbar.Collapse id="navbarNav">
            <Nav className="mx-auto">
              <Nav.Link className="nav-item w-100 m-3 d-flex justify-content-center no-wrap" href="#" active>
                <Link to="/create-post">Enlist</Link>
              </Nav.Link>
              <Nav.Link className="nav-item m-3">
                <Link to="listings">Listings</Link>
              </Nav.Link>
              <Nav.Link href="#service" className="nav-item m-3">
                Services
              </Nav.Link>
              <Nav.Link className="nav-item w-100 m-3 d-flex justify-content-center" href="#contactInfo" active>
                Contact
              </Nav.Link>
              {!isLoggedIn && (
                <Nav.Link className="nav-item m-3" onClick={handleSignupToggle}>
                  Register
                </Nav.Link>
              )}
            </Nav>
            <div className="no-display row">
              <div className="col-6">
                {isLoggedIn ? (
                  <Nav.Link className="nav-item m-3">
                    <button type="button" className="btn sign-in btn-primary" onClick={handleLogout}>
                      Log Out
                    </button>
                  </Nav.Link>
                ) : (
                  <Nav.Link className="nav-item m-3">
                    <button type="button" className="btn sign-in btn-primary" onClick={handleLoginToggle}>
                      Log In
                    </button>
                  </Nav.Link>
                )}
              </div>
            </div>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Modal className="text-center modal-card mx-auto d-flex justify-content-center border-0" show={showMenuModal} onHide={handleMenuModalClose} centered>
        <Modal.Body>
          <Nav className="flex-column">
            <Nav.Link onClick={handleMenuModalClose}>
              <Link to="/create-post">Enlist</Link>
            </Nav.Link>
            <Nav.Link onClick={handleMenuModalClose}>
              <Link to="/listings">Listings</Link>
            </Nav.Link>
            {!isLoggedIn && (
              <Nav.Link href="/register" onClick={handleMenuModalClose}>
                Register
              </Nav.Link>
            )}
          </Nav>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
          {!isLoggedIn ? (
            <Link to="/login">
              <button type="button" className="btn sign-in btn-primary btn-lg">
                Sign In
              </button>
            </Link>
          ) : (
            <button type="button" className="btn sign-in btn-primary btn-lg" onClick={handleLogout}>
              Log Out
            </button>
          )}
        </Modal.Footer>
      </Modal>

      <Modal className="text-center d-flex justify-content-center w-100" show={showLoginModal} onHide={handleLoginModalClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Log In</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="login-modal-content">
            <Login />
          </div>
        </Modal.Body>
      </Modal>

      <Modal className="text-center d-flex justify-content-center w-100" show={showSignupModal} onHide={handleSignupModalClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Sign Up</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="login-modal-content">
            <Register />
          </div>
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default NavBar;
